      <el-row type="flex" :gutter="20" justify="left">
        <el-col :span="16">
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')">登録</el-button>
            <el-button type="success" @click="resetForm('ruleForm')">納品書PDF発行</el-button>
            <el-button type="success" @click="resetForm('ruleForm')">検収書PDF発行</el-button>
            <el-button type="success" @click="toInvoiceForm">入金</el-button>
            <el-button type="danger" @click="resetForm('ruleForm')">注文廃棄</el-button>
          </el-form-item>
