<script>
import PageHeader from 'components/common/menu/PageHeader'
import {getEstimateDataOne, getEstimateDetailsDataAll} from 'api/estimates'
import {postDeliveryData, getDeliveryDataOne} from 'api/delivery'

export default {
  name: 'DeliveryForm',
  components: {
    PageHeader
  },
  data() {
    return {
      ruleForm: {
        DeliveryCode: this.$route.query.DeliveryCode,
        ProjectName: this.$route.query.ProjectName,
        ProjectCode: this.$route.query.ProjectCode,
        EstimateCode: this.$route.query.EstimateCode,
        CustomerName: this.$route.query.CustomerName,
        Deliverables1: '',
        Deliverables2: '',
        Deliverables3: '',
        Quantity1: '',
        Quantity2: '',
        Quantity3: '',
        Memo1: '',
        Memo2: '',
        Memo3: '',
        Remarks: ''
      },
      rules: {
        CustomerName: [
          {required: true, message: '得意先名を入力してください', trigger: 'change'}
        ],
        CustomerAddress: [
          {required: true, message: '得意先住所を入力してください', trigger: 'blur'}
        ],
        Work: [
          {required: true, message: '作業内容を入力してください', trigger: 'blur'}
        ],
        WorkTime: [
          {required: true, message: '作業期間を入力してください', trigger: 'blur'}
        ],
        PersonnelI: [
          {required: true, message: '主任担当者(甲)を入力してください', trigger: 'blur'}
        ],
        PersonnelII: [
          {required: true, message: '主任担当者(乙)を入力してください', trigger: 'blur'}
        ],
        Deliverables: [
          {required: true, message: '成果物を入力してください', trigger: 'blur'}
        ],
        DeliverableSpace: [
          {required: true, message: '納入場所を入力してください', trigger: 'blur'}
        ],
        Commission: [
          {required: true, message: '委託料を入力してください', trigger: 'blur'}
        ],
        PaymentDate: [
          {required: true, message: '支払期日を入力してください', trigger: 'blur'}
        ],
        AcceptanceConditions: [
          {required: true, message: '検収条件を入力してください', trigger: 'blur'}
        ]
      }
    }
  },
  computed: {
    newDateTime() {
      var date = new Date()
      var year = date.getFullYear()
      var month = date.getMonth() + 1
      var strDate = date.getDate()
      var hours = date.getHours()
      var minutes = date.getMinutes()

      if (month >= 1 && month <= 9) {
        month = '0' + month
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = '0' + strDate
      }
      var currentdate = year + '-' + month + '-' + strDate + ' ' + hours + ':' + minutes

      return currentdate
    }
  },
  created: function () {
    if (!this.$route.query.ProjectCode) {
      this.getDeliveryDataOne()
    }
    this.getEstimateDataOne()
    this.getEstimateDetailsDataAll()
  },
  methods: {
    getEstimateDataOne() {
      let values = {}
      values['EstimateCode'] = this.$route.query.EstimateCode
      getEstimateDataOne(values).then(res => {
        this.ruleForm.Deliverables1 = res.data.data[0].Deliverables1
        this.ruleForm.Deliverables2 = res.data.data[0].Deliverables2
        this.ruleForm.Deliverables3 = res.data.data[0].Deliverables3
        this.ruleForm.Quantity1 = res.data.data[0].Quantity1
        this.ruleForm.Quantity2 = res.data.data[0].Quantity2
        this.ruleForm.Quantity3 = res.data.data[0].Quantity3
      }).catch(err => {
        console.log(err)
      })
    },
    getEstimateDetailsDataAll() {
      let values = {}
      values['EstimateCode'] = this.$route.query.EstimateCode
      getEstimateDetailsDataAll(values).then(res => {
        console.log(res.data)
      }).catch(err => {
        console.log(err)
      })
    },
    getDeliveryDataOne() {
      let values = {}
      values['DeliveryCode'] = this.$route.query.DeliveryCode
      getDeliveryDataOne(values).then(res => {
        console.log(res.data.data)
        this.ruleForm.DeliveryCode = res.data.data[0].DeliveryCode
        this.ruleForm.ProjectName = res.data.data[0].ProjectName
        this.ruleForm.ProjectCode = res.data.data[0].ProjectCode
        this.ruleForm.EstimateCode = res.data.data[0].EstimateCode
        this.ruleForm.CustomerName = res.data.data[0].CustomerName
        this.ruleForm.Deliverables1 = res.data.data[0].Deliverables1
        this.ruleForm.Deliverables2 = res.data.data[0].Deliverables2
        this.ruleForm.Deliverables3 = res.data.data[0].Deliverables3
        this.ruleForm.Quantity1 = res.data.data[0].Quantity1
        this.ruleForm.Quantity2 = res.data.data[0].Quantity2
        this.ruleForm.Quantity3 = res.data.data[0].Quantity3
        this.ruleForm.Memo1 = res.data.data[0].Memo1
        this.ruleForm.Memo2 = res.data.data[0].Memo2
        this.ruleForm.Memo3 = res.data.data[0].Memo3
        this.ruleForm.Remarks = res.data.data[0].Remarks
      }).catch(err => {
        console.log(err)
      })
    },
    postDeliveryData() {
      let values = {}
      values['DeliveryCode'] = this.ruleForm.DeliveryCode
      values['EstimateCode'] = this.ruleForm.EstimateCode
      values['ProjectCode'] = this.ruleForm.ProjectCode
      values['ProjectName'] = this.ruleForm.ProjectName
      values['CustomerName'] = this.ruleForm.CustomerName
      values['Deliverables1'] = this.ruleForm.Deliverables1
      values['Deliverables2'] = this.ruleForm.Deliverables2
      values['Deliverables3'] = this.ruleForm.Deliverables3
      values['Quantity1'] = this.ruleForm.Quantity1
      values['Quantity2'] = this.ruleForm.Quantity2
      values['Quantity3'] = this.ruleForm.Quantity3
      values['Memo1'] = this.ruleForm.Memo1
      values['Memo2'] = this.ruleForm.Memo2
      values['Memo3'] = this.ruleForm.Memo3
      values['Remarks'] = this.ruleForm.Remarks
      values['DateTime'] = this.newDateTime
      postDeliveryData(values).then(res => {
        if (res.data.code === '200') {
          this.$router.push({
            path: 'Projectform',
            query: {ProjectCode: this.ruleForm.ProjectCode, activeName: 'third'}
          })
        }
      }).catch(err => {
        console.log(err)
      })
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert('submit!')
          this.postDeliveryData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    newProjectcode(param) {
      var date = new Date()
      var year = date.getFullYear()
      var month = date.getMonth() + 1
      var strDate = date.getDate()
      var hours = date.getHours()
      var minutes = date.getMinutes()
      var seconds = date.getSeconds()

      if (month >= 1 && month <= 9) {
        month = '0' + month
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = '0' + strDate
      }
      var currentdate = param + year + month + strDate + hours + minutes + seconds

      return currentdate
    },
    toInvoiceForm() {
      this.$router.push({
        path: 'Invoiceform',
        query: {
          ProjectCode: this.ruleForm.ProjectCode,
          ProjectName: this.ruleForm.ProjectName,
          EstimateCode: this.ruleForm.EstimateCode,
          DeliveryCode: this.ruleForm.DeliveryCode,
          InvoiceCode: this.newProjectcode('Inv'),
          CustomerName: this.ruleForm.CustomerName
        }
      })
    }
  }
}
</script>

