import {request} from 'network/request'

export function postDeliveryData (parameter) {
  return request({
    url: '/PostDeliveryData',
    method: 'post',
    data: parameter
  })
}

export function getDeliveryDataAll (parameter) {
  return request({
    url: '/GetDeliveryDataAll',
    method: 'post',
    data: parameter
  })
}

export function getDeliveryDataOne (parameter) {
  return request({
    url: '/GetDeliveryDataOne',
    method: 'post',
    data: parameter
  })
}
